.ExperimentContainer {
  display: flex;
  justify-content: center;

  img {
    width: 600px;
    padding: 5px;
  }

  .Description {
    display: flex;
    align-items: flex-start;
    flex-direction: column;

    margin-left: 100px;
    max-width: 500px;

    h1 {
      font-size: 45px;
      color: #151922;
      text-align: start;
      font-weight: 500;
    }

    p {
      text-align: start;
      font-size: 16px;
      font-weight: 400;
      margin: 5px 0;
      line-height: 1.4;
    }
  }
}

.rodal-dialog {
  height: 500px !important;
  width: 900px !important;
  top: 40px;
}

.Experiment {
  text-align: left;
  padding: 0 30px;
  margin: 0 31px;
  height: 100%;
  overflow: auto;

  .Row {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0;
    margin-bottom: 18px;
  }

  img,
  video {
    height: auto;
    max-width: 60%;
    object-fit: contain;
    margin: 10px 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
  }

  p {
    text-align: justify;
    font-size: 16px;
    margin: 0;
    margin-bottom: 18px;
  }

  h1 {
    font-weight: 500;
    font-size: 35px;
    text-align: center;
    margin: 36px 0 12px 0;
  }

  h2 {
    margin: 0;
    margin-bottom: 6px;
    font-weight: 500;
  }
}

@media only screen and (max-width: 1050px) {
  .ExperimentContainer {
    width: 100vw;
    padding: 0 15px;
    box-sizing: border-box;
    flex-direction: column-reverse;
    align-items: center;

    .rodal-dialog {
      height: 80vh;
      width: 85vw;
    }

    .Description {
      margin: 0;
      align-items: center;

      h1 {
        font-size: 30px;
        text-align: center;
        width: 100%;
      }
    }

    img {
      width: 100%;
      max-width: 500px;
      padding: 5px;
      margin-top: 10px;
    }
  }

  .rodal-dialog {
    height: 500px !important;
    width: 320px !important;
  }

  .Experiment {
    padding: 0 20px 10px 10px;
    margin: 0;

    .Row {
      flex-wrap: wrap;
    }

    img,
    video {
      max-width: 80vw;
      margin: 10px 0;
    }

    p {
      font-size: 14px;
    }

    h1 {
      font-size: 25px;
      margin: 12px;
    }

    h2 {
      font-size: 20px;
    }
  }
}
